(function(angular) {
	'use strict';

	angular
		.module('yoophawebapp')
		.directive('chatHead', chatHead);

	/** @ngInject */
	function chatHead(){
		var directive = {
			restrict: 'E',
			templateUrl: 'app/components/chatHead/chatHead.html',
			bindToController: {
				name: "@"
			},
			controller: chatController,
      controllerAs: 'chat',
      scope: true,
      transclude: true
		};
		// console.log('chat directive');
		return directive;
		/** @ngInject */
    function chatController($scope /*, chatService*/) {
    	// console.log('chat controller');
      var vm = this; 
      vm.toggle = toggle;
      vm.currState = { status : 'closed', statusCode : 0, icon : 'fa-angle-up' };
      vm.usrMsg = '';
      // vm.send = send;
      vm.isClosed = false;
      vm.closeWindow = closeWindow;
      vm.data = {
      	from: 'user',
      	date: 'date',
      	msg: '',
      	chatName: '0321456987'
      };
      vm.chatHistory = [
      {from: 'user', date: '23-03-2017', time: '16:13', msg: 'this is a test message for the porpose of testing', chatName: "0321456987"},
      {from: 'master', date: '23-03-2017', time: '16:19', msg: 'this is a test message from the master', chatName: "0321456987"}];

    //   chatService.on('message', function (data) {
	   //    // console.log(data);
	   //    addConversation(data);
  	 //    // chatService.emit('user_msg', { my: 'data' });
  		// });

  		function addConversation(msg){
  			if(msg.from == 'master'){
  				msg.date = getDate();
  				msg.time = 'time';
  				vm.chatHistory.push(msg);
  			}else if(msg.from == 'user'){
  				// console.log(msg);
  				vm.chatHistory.push(msg);
  				vm.usrMsg = '';
  			}
  		}

      function toggle(){
      	// console.log('toggle started');
      	if(!vm.currState.statusCode) {
      		vm.currState.statusCode = 1;
      		vm.currState.status = 'opened';
      		vm.currState.icon = 'fa-angle-down'
      	} else {
      		vm.currState.statusCode = 0;
      		vm.currState.status = 'closed';
      		vm.currState.icon = 'fa-angle-up'
      	}
      }
      function closeWindow(flag){
      	if(flag){
      		vm.isClosed = true;
      	} else {
      		vm.isClosed = false;
      	}
      }

      function getDate() {
      	var d = new Date();
      	return d;
      }

      // function send(input) {
      // 	// console.log(input);
      // 	vm.data.msg = input;
      // 	vm.data.date = getDate();
      // 	chatService.emit('message', vm.data);
      // }
    }

	}
})(angular);
(function(angular) {
    'use strict';

    angular
        .module('yoophawebapp')
        .controller('addressController', addressController);

    /** @ngInject */
    function addressController($rootScope, $scope, $state, $localStorage, cartService, accountService) {
        var vm = this;
        var usr = {};
        vm.custInfo = {
        	address: {}
        };
        vm.cust = {};
        vm.address_arr = [];
        vm.saveChanges = saveChanges;
        vm.route = route;

        init();

        function init() {
        	usr = $localStorage['usr'];
        	vm.custInfo.name = usr.username;
        	vm.custInfo.address.address_type = "3";
        	vm.custInfo.address.state = 'Tamil Nadu';
        	vm.custInfo.address.country = 'India';
        	vm.custInfo.address.district = 'Chennai';
        	vm.custInfo.address.lat = '0.000'; 
        	vm.custInfo.address.long = '0.000';
        	getCust();
        }

        function route(){
            $state.go('account');
        }

        function getCust() {
    	    cartService.getCustomerIfExist(usr.usr_id)
    	        .then(function(res) {
    	            // console.log(res);
    	            if (angular.isDefined(res.data.results[0])) {
    	                $localStorage['cust'] = res.data.results[0];
    	                vm.custInfo.name = res.data.results[0].name;
    	                vm.cust = res.data.results[0];
    	                vm.address_arr = res.data.results[0].address;
    	            } else {
    	            	vm.custInfo.name = usr.username;
    		            vm.custInfo.mobile = usr.mobile_no;
    	            }
    	        });
        }

        function saveChanges(){
        	var data = vm.cust;
        	data.address = [vm.custInfo.address];
        	accountService.updateCust(vm.cust.id, data)
        		.then(function (res){
        			console.log(res);
        		});
        }

    }
})(angular);
