(function(angular) {
    'use strict';

    /**
    * @ngdoc service
    * @name 'Uploading service'
    * @description
    * Service in the yoophaWeb.
    */
    angular.module('yoophawebapp')
        .service('viewCartService', viewCartService);

    /** @ngInject */
    function viewCartService($q, $localStorage, getData, postData, utilities) {

        var service = {};
        var url = '';

        service.masterCart = undefined;
        service.isMasterCartExist = undefined;
        service.createMasterCart = createMasterCart;
        service.getMasterCart = getMasterCart;
        service.delCartItems = delCartItems;
        service.updateCartInfo = updateCartInfo;
        service.updateCartItem = updateCartItem;
        return service;

        function getMasterCart(id){
            service.isMasterCartExist = undefined;
            url = 'https://www.yoopha.com/shim/cartinfo.php/?actiontype=3&subtype=1&uid=' + id + '&token=' + $localStorage['usr'].accessTok;
            return returnData('isMasterCartExist');   
        }

        function createMasterCart(data){
            url = 'https://www.yoopha.com/shim/cartinfo.php/?actiontype=3&subtype=2&token=' + $localStorage['usr'].accessTok;
            var deferred = $q.defer();
            postData.async(url, data)
                .then(function(res) {
                // if(res.status == 201 && res.statusText == 'Created')
                    deferred.resolve(res.data);
                // else 
                    // deferred.reject(res.data);
                });
            return deferred.promise;
        }

        function delCartItems(id, cart_type){
            if(cart_type == 1) {
                // url = 'http://ec2-35-154-138-58.ap-south-1.compute.amazonaws.com/api/v1/cart/' + id + '/delete_cartitem/';
                url = 'https://www.yoopha.com/shim/cartinfo.php/?actiontype=1&subtype=4&token=' + $localStorage['usr'].accessTok + '&cart_item_id=' + id;
            }else {
                // url = 'http://ec2-35-154-138-58.ap-south-1.compute.amazonaws.com/api/v1/cart/' + id + '/delete_otccartitem/';
                url = 'https://www.yoopha.com/shim/cartinfo.php/?actiontype=2&subtype=4&token=' + $localStorage['usr'].accessTok + '&otc_item_id=' + id;
            }
            var deferred = $q.defer();
            getData.async(url)
                .then(function (res) {
                    // if(res.status == 204){
                        deferred.resolve('success');
                    // } else {
                        // deferred.reject('error');
                    // }
                });
            return deferred.promise;
        }

        function updateCartItem(id, data, cart_type) {
            var deferred = $q.defer();
            if(cart_type == 1)
                url = 'https://www.yoopha.com/shim/cartinfo.php/?actiontype=1&subtype=5&token=' + $localStorage['usr'].accessTok + '&cart_item_id=' + id;
            else 
                url = 'https://www.yoopha.com/shim/cartinfo.php/?actiontype=2&subtype=5&token=' + $localStorage['usr'].accessTok + '&otc_item_id=' + id;
            postData.async(url, data)
                .then(function (res){
                    deferred.resolve(res);
                }, function (err){
                    deferred.reject(err);
                })
            return deferred.promise;
        }

        function updateCartInfo(id, data, cart_type){
            var deferred = $q.defer();
            if(cart_type == 1){
                // data.cartitems = [];
                url = 'https://www.yoopha.com/shim/cartinfo.php/?actiontype=1&subtype=6&token=' + $localStorage['usr'].accessTok + '&cart_id=' + id;
            } else {
                // data.otccartitems = []; 
                url = 'https://www.yoopha.com/shim/cartinfo.php/?actiontype=2&subtype=6&token=' + $localStorage['usr'].accessTok + '&otc_id=' + id;
            }
            postData.async(url, data)
                .then(function (res){
                    deferred.resolve(res);
                }, function (err){
                    deferred.reject(err);
                })
            return deferred.promise;
        }

        function returnData(data) {
            var deferred = $q.defer();

            if (!service[data])  {
                getData.asyncHead(url).then(function (response) {
                if (response.length > 0) {
                    service[data] = response.data.results;
                    deferred.resolve(response);
                } else if (angular.isObject(response)) {
                    service[data] = response.data.results;
                    deferred.resolve(response);
                  } else {
                    deferred.reject(response);
                }
                });
          } else {
            deferred.resolve(service[data]);
          }

          return deferred.promise;
        }
    }
})(angular);
(function(angular) {
  'use strict';

  angular
  .module('yoophawebapp')
  .controller('ViewCartController', ViewCartController);

    /** @ngInject */
    function ViewCartController($scope, $state, $localStorage, viewCartService, homeService) {
        var vm = this;
        var incomplete_cart = [];
        var userinfo;

        vm.viewData = {};
        vm.mastercartIfExist = undefined;
        vm.createMasterCart = createMasterCart;
        vm.getMasterCart = getMasterCart;
        vm.deleteCart = deleteCart;
        vm.updateQty = updateQty;

        init();

        function init() {
        	userinfo = $localStorage['usr'];
            homeService.getExistingOtcCart(userinfo.usr_id)
                .then(function (res) {
                    $localStorage['incomp_cart'][1] = homeService.existingOtcCartInfo.results[0];
                    incomplete_cart = $localStorage['incomp_cart'];
                    getCart();
                    getMasterCart();
            });

        }

        function getMasterCart() {
            viewCartService.getMasterCart(userinfo.usr_id)
                .then(function (res){
                    if(viewCartService.isMasterCartExist[0] != null){
                        vm.mastercartIfExist = viewCartService.isMasterCartExist[0];
                        $localStorage['masterCart'] = viewCartService.isMasterCartExist[0];
                    }
                    else{
                        vm.mastercartIfExist = undefined;
                        delete $localStorage['masterCart'];
                    }
                });
        }

        function getuser(){
        	homeService.getUser(userinfo.usr_id)
        		.then(function (res){
        			userinfo.username = viewCartService.userData.username;
        			userinfo.email = viewCartService.userData.email;
        			userinfo.mobile_no = viewCartService.userData.mobile_no;
        			$localStorage['usr'] = userinfo;
        		})
        }

        function getCart() {
            console.log(incomplete_cart);
            if(angular.isDefined(incomplete_cart)){
                vm.viewData.p_cart = incomplete_cart[0];
                vm.viewData.o_cart = incomplete_cart[1];
                getTot();
            }
        }

        function getTot() {
            if(vm.viewData.p_cart.cartitems.length > 0 && vm.viewData.o_cart.otccartitems.length > 0) {
                vm.tot = parseInt(vm.viewData.p_cart.tot_presc_amt) + parseInt(vm.viewData.o_cart.tot_otc_amount) + 10;
            } else if(vm.viewData.p_cart.cartitems.length > 0 && vm.viewData.o_cart.otccartitems.length <= 0) {
                vm.tot = parseInt(vm.viewData.p_cart.tot_presc_amt) + 10;
            } else if(vm.viewData.o_cart.otccartitems.length <= 0 && vm.viewData.o_cart.otccartitems.length > 0) {
                vm.tot = parseInt(vm.viewData.o_cart.tot_otc_amount) + 10;
            }
        }

        function createMasterCart() {
        	if(angular.isUndefined($localStorage['masterCart'])){
        		var data = {
        			'otccart': vm.viewData.o_cart.id,
        			'user': userinfo.usr_id,
        			'prescart': vm.viewData.p_cart.id
        		};
        		viewCartService.createMasterCart(data)
        			.then(function (res){
        				$localStorage['masterCart'] = res;
        				$state.go('Cart');
        			}, function (err){
        				alert(err);
        			})
        	} else {
        		$state.go('Cart');
        	}
        }

        function hasID(list, item){
            if(list.length != 0){
                for(var i = 0; i < list.length; i++){
                    if(list[i].id == item.id)
                        return i;
                }
                return -1;
            } else {
                return -1;
            }
        }

        function deleteCart(item, index_id){
            var index = hasID(index_id == 1 ? vm.viewData.p_cart.cartitems : vm.viewData.o_cart.otccartitems, item);
            viewCartService.delCartItems(item.id, index_id)
                .then(function (res) {
                    if(index_id == 1){
                        if(index != -1){
                            vm.viewData.p_cart.cartitems.splice(index, 1);
                            $localStorage['incomp_cart'][0].cartitems = vm.viewData.p_cart.cartitems;
                        }
                    }else {
                        if(index != -1){
                            vm.viewData.o_cart.otccartitems.splice(index, 1);
                            $localStorage['incomp_cart'][1].otccartitems = vm.viewData.o_cart.otccartitems;
                        }
                    }
                    updateTotPrice(index_id);
                }, function (err){

                })
        }

        function updateTotPrice(type){
            var temp_ptot = 0;
            var temp_ctot = 0;
            for (var i = 0; i < vm.viewData.p_cart.cartitems.length; i++) {
                temp_ptot += (parseInt(vm.viewData.p_cart.cartitems[i].price) * vm.viewData.p_cart.cartitems[i].quantity);
            } 
            vm.viewData.p_cart.tot_presc_amt = temp_ptot;
            for (var i = 0; i < vm.viewData.o_cart.otccartitems.length; i++) {
                temp_ctot += (parseInt(vm.viewData.o_cart.otccartitems[i].price) * vm.viewData.o_cart.otccartitems[i].quantity);   
            }
            vm.viewData.o_cart.tot_otc_amount = temp_ctot;
            updateCartInfo(type);            
            getTot();
        }

        function updateCartInfo(type) {
            var data = {};
            var id = 0;
            if(type == 1){
                id = vm.viewData.p_cart.id;
                angular.copy(vm.viewData.p_cart, data);
                data.cartitems = [];
            }else {
                id = vm.viewData.o_cart.id;
                angular.copy(vm.viewData.o_cart, data);
                data.otccartitems = [];
            }
            viewCartService.updateCartInfo(id, data, type)
                .then(function(res){
                    console.log(res);
                })
        }

        function updateQty(item, action, cart_type) {
            if(action){
                item.quantity++;
            }else {
                if(item.quantity <= 1){
                    del_selected_drug(item);
                }else {
                    item.quantity--;
                }
            }
            var temp = {};
            angular.copy(item, temp);
            if(cart_type == 1){
                var temp_id = temp.product.id;
                temp.product = temp_id;
                viewCartService.updateCartItem(temp.id, temp, cart_type)
                    .then(function (res){
                        var index = hasID(vm.viewData.p_cart.cartitems, item);
                        $localStorage['incomp_cart'][0].cartitems[index] = item;
                        updateTotPrice(cart_type); 
                    }, function (err) {
                       item.quantity =  action ? (item.quantity <= 1 ? item.quantity : item.quantity--) : item.quantity ++;
                    })
            }else {
                var temp_id = temp.otcproduct.id;
                temp.otcproduct = temp_id;
                viewCartService.updateCartItem(temp.id, temp, cart_type)
                    .then(function (res){
                        var index = hasID(vm.viewData.o_cart.otccartitems, item);
                        $localStorage['incomp_cart'][1].otccartitems[index] = item;
                        updateTotPrice(cart_type);
                    }, function (err) {
                       item.quantity =  action ? (item.quantity <= 1 ? item.quantity : item.quantity--) : item.quantity ++;
                    })
            }
        }

        function updatePrice(item) {
            console.log(parseInt(item.price) / item.quantity);
            return  (parseInt(item.price) / item.quantity);
        }

    }
})(angular);

