(function(angular) {
    'use strict';

    /**
    * @ngdoc service
    * @name 'Uploading service'
    * @description
    * Service in the yoophaWeb.
    */
    angular.module('yoophawebapp')
        .service('registerService', registerService);

    /** @ngInject */
    function registerService($q, $localStorage, postData, getData, utilities) {
        var service = {};
        var url = '';
        service.register = register;
        service.sendOTP = sendOTP;
        return service;

        function register(data) {
            url = 'https://www.yoopha.com/shim/login.php?actiontype=2';
            var deferred = $q.defer();
            postData.async(url, data, 1)
                .then(function(res) {
                    // console.log(res);
                if(res.status == 200)
                    deferred.resolve(res.data);
                else 
                    deferred.reject(res.data);
                });
            return deferred.promise;
        }

        function sendOTP(mob) {
            var deferred = $q.defer();
            var otp =  utilities.genOTP('naa_tha_parrupu');
            url = 'https://www.yoopha.com/shim/login.php?actiontype=3';
            // console.log(url);
            // return otp;
            var data = {
                'ref': otp,
                'mob': mob
            };
            postData.async(url, data, 1)
                .then(function(res){
                    console.log(res);
                    deferred.resolve(otp);
                });
            return deferred.promise;
        }
    }
})(angular);
