(function(angular) {
  	'use strict';


  	angular.module('yoophawebapp')
    	.service('homeService', homeService);

	/** @ngInject */
	function homeService($q, $http, $localStorage, $uibModal, getData, utilities, postData) {

	    var service = {};

	    var url;
	    service.user = undefined;
	    service.fav = undefined;
	    service.pharmacy = undefined;
	    service.existingCartInfo = undefined;
	    service.existingOtcCartInfo = undefined;
	    service.del = [];
	    service.getPharmacy = getPharmacy;
	    service.getUser = getUser;
	    service.getExistingCart = getExistingCart;
	    service.getExistingOtcCart = getExistingOtcCart;
	    service.incomplete_alert = incomplete_alert;
	    service.delCart = delCart;
	    service.getUserFav = getUserFav; 
	    return service;


	    function returnData(data) {
	      var deferred = $q.defer();

	      if (!service[data])  {
	        getData.async(url).then(function (response) {
	          if (response.length > 0) {
	            service[data] = response;
	            deferred.resolve(response);
	          } else if (angular.isObject(response)) {
	            service[data] = response;
	            deferred.resolve(response);
	          } else {
	            deferred.reject(response);
	          }
	        });
	      } else {
	        deferred.resolve(service[data]);
	      }

	      return deferred.promise;
	    }

	    function getUserFav(id){
	    	service.fav = undefined;
	    	url = 'https://www.yoopha.com/shim/user.php/?actiontype=3&subtype=1&token=' + $localStorage['usr'].accessTok + '&uid=' + id;
	      	return returnData('fav');
	    }

	    function getUser(id){
		    url = 'https://www.yoopha.com/shim/user.php/?actiontype=1&uid=' + id + '&token=' + $localStorage['usr'].accessTok;
		    return returnData('user');
	    }

	    function getPharmacy(id){
	    	url = 'https://www.yoopha.com/shim/common.php/?actiontype=1&subtype=1&pharma_id=' + id;
	    	return returnData('pharmacy');
	    }

	    function getExistingCart(id) {
	    	service.existingCartInfo = undefined;
		    url = 'https://www.yoopha.com/shim/cartinfo.php/?actiontype=1&subtype=1&uid=' + id + '&token=' + $localStorage['usr'].accessTok;
		    return returnData('existingCartInfo');
	    }

	    function getExistingOtcCart(id){
	    	url = 'https://www.yoopha.com/shim/cartinfo.php/?actiontype=2&subtype=1&uid=' + id + '&token=' + $localStorage['usr'].accessTok;;
		    return returnData('existingOtcCartInfo');	
	    }

	    function incomplete_alert(){
	    	var modalInstance = $uibModal.open({
	    	  animation: true,
	    	  backdrop: 'static',
	    	  templateUrl: 'app/components/template/commonTemp.html',
	    	  controller: 'templateController',
	    	  controllerAs: 'tempMod',
	    	  size: 'md'
	    	});
	    }

	    function delCart(){
	    	var deferred = $q.defer();
	    	var cart_ids = $localStorage['incomp_cart'];
	    	for(var i = 0; i < cart_ids.length; i++){
	    		url = 'https://www.yoopha.com/shim/cartinfo.php/?actiontype=1&subtype=2&cart_id=' + cart_ids[i].id + '&token=' + $localStorage['usr'].accessTok;
		    	getData.async(url).then(function (response) {
	    			if(response.status == 204){
	    				service.del.push(response);
	    			}
	    			deferred.resolve(response);
		    	});
	    	}
	    	return deferred.promise;
	    }
	}
})(angular);
(function(angular) {
    'use strict';

    angular
        .module('yoophawebapp')
        .controller('homeController', homeController);

    /** @ngInject */
    function homeController($scope, $rootScope, $state, $localStorage, homeService) {
        var vm = this;
        var img = ['/assets/images/img1.png', '/assets/images/img1.png', '/assets/images/img1.png', '/assets/images/img1.png', '/assets/images/img1.png'];
        var slides = vm.slides = [];
        var currIndex = 0;
        vm.noWrapSlides = false;
        vm.active = 0;
        vm.pendingCart = {};
        vm.route = function (){
          $state.go('viewcart');
        }

        vm.addSlide = function() {
          var newWidth = 600 + slides.length + 1;
          slides.push({
            image: img[slides.length % 5],
            id: currIndex++
          });
        };

        init();

        function init() {
          var status = $localStorage['log_status'];
          for (var i = 0; i < 4; i++) {
            vm.addSlide();
          }

          if(angular.isDefined(status)){
            vm.isLoggedIn = status ? true : false;
            if(vm.isLoggedIn) {
              $rootScope.logStat = true;
              getUserInfo();
              getCartInfo();
              fillFav();
            }
            $rootScope.isCartAdded = angular.isDefined($localStorage['incomp_cart']);
          }

          function getCartInfo(){
            var u_temp_data = $localStorage['usr'];
            if(angular.isDefined(u_temp_data)){
            var user_id = u_temp_data.usr_id;
            homeService.getExistingCart(user_id)
              .then(function (res) {
                // console.log(homeService.existingCartInfo);
                if(homeService.existingCartInfo.results.length < 1) {
                  $rootScope.isCartAdded = false;
                  delete $localStorage.incomp_cart;
                }else {
                  vm.pendingCart.cart = homeService.existingCartInfo.results;
                  getPharma(vm.pendingCart.cart);
                  $localStorage['incomp_cart'] = [homeService.existingCartInfo.results[0]];
                  getOtcCartInfo();
                  // $rootScope.isCartAdded = true;
                }
              });
            }
          }

          function getOtcCartInfo(){
            var u_temp_data = $localStorage['usr'];
            if(angular.isDefined(u_temp_data)){
            var user_id = u_temp_data.usr_id;
            homeService.getExistingOtcCart(user_id)
              .then(function (res) {
                if(homeService.existingOtcCartInfo.results.length > 0) {
                  $localStorage['incomp_cart'][1] = homeService.existingOtcCartInfo.results[0];
                }
                  $rootScope.isCartAdded = true;
              });
            }
          }

          function getPharma(data){
            homeService.getPharmacy(data[0].pharmacy)
                .then(function (res){
                    vm.pendingCart.shop = res
                    // console.log(res);
                    $localStorage['selectedShop'] = res;
                })
          }

          function getUserInfo() {
            var u_temp_data = $localStorage['usr'];
            if(angular.isDefined(u_temp_data)){
            var user_id = u_temp_data.usr_id;
            homeService.getUser(user_id)
              .then(function (res) {
                // console.log(homeService.user);
                u_temp_data.username = homeService.user.username;
                u_temp_data.email = homeService.user.email;
                u_temp_data.mobile_no = homeService.user.mobile_no;
                $localStorage['usr'] = u_temp_data;
              }, function(res) {
                console.log(res);
              })
            }
          }

          function fillFav() {
            var u_temp_data = $localStorage['usr'];
            if(angular.isDefined(u_temp_data)){
            var user_id = u_temp_data.usr_id;
            homeService.getUserFav(user_id)
              .then(function (res) {
                $localStorage['fav'] = res.results;
              }, function(res) {
                console.log(res);
              })
            } 
          }

        }

    }
})(angular);


(function(angular) {
  'use strict';


  angular.module('yoophawebapp')
  	.service('dymService', dymService);

  /** @ngInject */
  function dymService($q,$localStorage, getData, postData) {
  	var service = {};
    var url = '';
    service.getphramacy = getphramacy;
    service.dumdata=dumdata;
   
  	

    function getphramacy(key){
      url = 'https://www.yoopha.com/shim/search.php/?actiontype=2&key='+key;
      var deferred = $q.defer();
    	getData.asyncHead(url)
    		.then(function(res){
          // console.log(res);
          deferred.resolve(res);
        })
      return deferred.promise;
    }

    function dumdata(data){
      url = 'https://www.yoopha.com/shim/common.php/?actiontype=2&token=' + $localStorage['usr'].accessTok;
      var deferred = $q.defer();
      postData.async(url, data)
        .then(function(res){
            // if(res.status == 201 && res.statusText == 'Created')
              deferred.resolve(res.data);
            // else 
              // deferred.reject(res.data);
        })
      return deferred.promise; 
    }

    return service;
  }
})(angular);
(function(angular) {
    'use strict';

    angular
        .module('yoophawebapp')
        .controller('dymController', dymController);

    /** @ngInject */
    function dymController($state,$scope, $log, $localStorage, $uibModal, dymService) {
        var vm = this;
        var img = ['/assets/images/dyum1.png','/assets/images/dyum1.png','/assets/images/dyum1.png','/assets/images/dyum1.png', '/assets/images/dyum1.png' ];
        var slides = vm.slides = [];
        var currIndex = 0;
        var selectedmedi = '';
        vm.noWrapSlides = false;
        vm.pageView = {
            donate : true,
            dropbox : false,
            rules : false,
            med_avil : false
        };
        vm.toggle = toggle;
        vm.active = 0;
        vm.searchdata = '';
        vm.selectedResult = selectedResult;
        vm.getphamalist = getphamalist;
        vm.quantity = '';
        vm.date = '';
        vm.medidata = [];
        vm.adddata = adddata;
        vm.del = del;
        vm.dumcreate = dumcreate;
        vm.hstep = 1;
        vm.mstep = 15;
        vm.ismeridian = false;
        vm.addSlide = function() {
            var newWidth = 600 + slides.length + 1;
            slides.push({
                image: img[slides.length % 5],
                id: currIndex++
            });
        };

        init();

        function init() {
            vm.dt = new Date();
            for (var i = 0; i < 5; i++) {
                vm.addSlide();
            }
            vm.mytime = new Date();
            vm.mytime.setHours(vm.mytime.getHours() + 2);
           
        }

        function clearview() {
            vm.pageView = {
                donate : false,
                dropbox : false,
                rules : false,
                med_avil : false
            };
        }

        function toggle(view) {
            clearview();
            vm.pageView[view] = true;
        }

        function del(index) {
            vm.medidata.splice(index, 1);
        }


        // $scope.update = function() {
        //     var d = new Date();
        //     d.setHours(14);
        //     d.setMinutes(0);
        //     vm.mytime = d;
        // };

        // $scope.changed = function() {
        //     $log.log('Time changed to: ' + vm.mytime);
        // };




        $scope.open1 = function() {
            $scope.popup1.opened = true;
        };

        $scope.open2 = function() {
            $scope.popup2.opened = true;
        };

        vm.setDate = function(year, month, day) {
            vm.dt = new Date(year, month, day);
        };

        $scope.formats = ['dd-MMMM-yyyy', 'yyyy/MM/dd', 'dd.MM.yyyy', 'shortDate'];
        $scope.format = $scope.formats[0];
        $scope.altInputFormats = ['M!/d!/yyyy'];

        $scope.popup1 = {
            opened: false
        };

        $scope.popup2 = {
            opened: false
        };

        var tomorrow = new Date();
        tomorrow.setDate(tomorrow.getDate() + 1);
        var afterTomorrow = new Date();
        afterTomorrow.setDate(tomorrow.getDate() + 1);
        $scope.events = [{
            date: tomorrow,
            status: 'full'
        }, {
            date: afterTomorrow,
            status: 'partially'
        }];

        function getDayClass(data) {
            var date = data.date,
                mode = data.mode;
            if (mode === 'day') {
                var dayToCheck = new Date(date).setHours(0, 0, 0, 0);

                for (var i = 0; i < $scope.events.length; i++) {
                    var currentDay = new Date($scope.events[i].date).setHours(0, 0, 0, 0);

                    if (dayToCheck === currentDay) {
                        return $scope.events[i].status;
                    }
                }
            }

            return '';
        }

        function getphamalist(key) {
            if (key.length >= 3) {
                dymService.getphramacy(key)
                    .then(function(res) {
                        vm.paharmaList = res.data.results;
                        console.log(vm.paharmaList);
                        $localStorage['medicneList'] = vm.paharmaList;
                    });
            } else {
                vm.paharmaList = [];
                console.log(vm.paharmaList);

            }
        }

        function openAckmodel(){
            var modalInstance = $uibModal.open({
                  animation: true,
                  backdrop: 'static',
                  templateUrl: 'app/components/template/dyumAck.html',
                  controller: 'templateController',
                  controllerAs: 'mod',
                  size: 'md'
                });

                modalInstance.result.then(function (selectedItem) {
                    $state.go('home');
                }, function () {
                  // $log.info('Modal dismissed at: ' + new Date());
                });
        }

        function selectedResult(dat) {
            vm.searchdata = dat.name;
            selectedmedi = dat;
            console.log(dat);
            // $localStorage['foundmedicine'] = vm.selectedmedi;
            // console.log(vm.selectedmedi);
            vm.paharmaList = '';
        }


        function adddata() {
            // vm.medidata=[];
            var index = hasId(vm.medidata, selectedmedi);
            if (index == -1) {
                var getentereddata = {
                    'Name': selectedmedi.name,
                    'product': selectedmedi.id,
                    'quantity': vm.quantity,
                    'expiry': formatDate(vm.date, 1),
                }
                vm.medidata.push(getentereddata);
                // console.log(vm.medidata);

            }
            cleartext();
        }

        function formatDate(str, flag) {
            if (flag) {
                var date = str.getDate();
                var month = str.getMonth() + 1;
                var year = str.getFullYear();
                return year + '-' + month + '-' + date;
            } else {
                var hour = str.getHours();
                var min = str.getMinutes();
                return hour + ':' + min;
            }
        }

        function cleartext() {
            vm.searchdata = '';
            vm.quantity = '';
            vm.date = '';
            selectedmedi = '';
        }

        function hasId(arr, item) {
            for (var i = 0; i < arr.length; i++) {
                if (vm.medidata[i].product == item.id) {
                    return i;
                }
            }
            return -1;
        }

        function dumcreate(data) {
            var tempDate = formatDate(vm.dt, 1);
            var tempTime = formatDate(vm.mytime, 0);
            var data = {
                "status": "1",
                "time_to_collect": tempDate + ' ' + tempTime,
                "user": $localStorage['usr'].usr_id,
                "customer_dyum_medicines": vm.medidata
            }
            dymService.dumdata(data)
                .then(function(res) {
                    console.log(res);
            });
            openAckmodel();
        }

    }
})(angular);
