(function(angular) {
    'use strict';

    angular
        .module('yoophawebapp')
        .controller('historyController', historyController);

    /** @ngInject */
    function historyController($scope, $localStorage, $state, historyService, cartService) {
        var vm = this;
        var userInfo = undefined;
        var customerInfo = undefined;
        vm.route = route;
        vm.orderList = [];

        init();

        function init() {
          userInfo = $localStorage['usr'];
          customerInfo = $localStorage['cust'];
          if(angular.isUndefined(customerInfo)){
            cartService.getCustomerIfExist(userInfo.usr_id)
              .then(function(res) {
                customerInfo = cartService.customer.results[0];
                $localStorage['cust'] = customerInfo;
                gethistory();
              })
            }else {
              gethistory();
            }
        }

        function route(data){
          // console.log(data);
          $state.go('order', {order: data.orderID});
        }

        function gethistory(){
           historyService.getActiveOrder(customerInfo.id)
            .then(function (res){
              vm.orderList = res.results;
              historyService.getHistory(customerInfo.id)
                .then(function (res){
                  // console.log(res.results);
                  vm.orderList = vm.orderList.concat(res.results);
                  console.log(vm.orderList);
                })
              // vm.orderList = res;
            })
        }
        
    }
})(angular);


