    function getLocation() {
      var deferred = $q.defer();

      if (navigator.geolocation) {
        navigator.geolocation.getCurrentPosition(success, error);
      } else {
        $log.warn('Geo location not availble.');
        deferred.reject('Geo location not availble.');
      }

      return deferred.promise;

      function error(error) {
        console.log(error);
        var confirm;
        var msg = '';
        switch (error.code) {
          case error.PERMISSION_DENIED:
            msg = 'Permission to access location denied. Provide permission and reload the page.';
            break;
          case error.POSITION_UNAVAILABLE:
            msg = 'Location information is unavailable.';
            break;
          case error.TIMEOUT:
            msg = 'The request to get user location timed out.';
            break;
          case error.UNKNOWN_ERROR:
            msg = 'Unable to get location. An unknown error occurred.';
            break;
        }

        $log.warn(msg);
        deferred.reject(msg);
      }

      function success(position) {
        deferred.resolve(position);
      }
    }

    function genID(str){
      var crc = crc ^ (-1);
      for (var i = 0, iTop = str.length; i < iTop; i++) {
        crc = (crc >>> 8) ^ b_table[(crc ^ str.charCodeAt(i)) & 0xFF];
      }

      return (crc ^ (-1)) >>> 0;
    }

    function genOTP(key){
      var epoch = Math.round(new Date().getTime() / 1000.0);
      var time = leftpad(dec2hex(Math.floor(epoch / 30)), 16, "0");
      var shaObj = new jsSHA("SHA-512", "TEXT");
      shaObj.setHMACKey(time, "TEXT");
      shaObj.update(base32tohex(key));
      var hmac = shaObj.getHMAC("HEX");
      var offset = hex2dec(hmac.substring(hmac.length - 1));
      var otp = (hex2dec(hmac.substr(offset * 2, 8)) & hex2dec("7fffffff")) + "";
      otp = (otp).substr(otp.length - 6, 6);
      return otp;
    }

